  height: 280px;
}
.codepencil.fullscreen {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100vh;
  width: 100vw;
  z-index: 999999;
  background-color: #fff;
}
.codepencil .overlays {
